import { useState } from 'react';


export default function MainOSP4114() {
  const dummy = [
    {
      title: "GS ",
      img: 'gs  '
    },
    {
      title: "",
      img: '  '
    },
    {
      title: " ",
      img: '   '
    },
  ];

  const [toggle, setToggle] = useState({
    index: 0,
    state: false
  });

  const onClick = (index: number, state = false) => {
    setToggle({
      ...toggle,
      index,
      state
    });
  };

  return <main className='main-osp4114'>
    <section className='certificate__container background-01'>
      <h2></h2>

      <ul className='certificate-list'>
        {
          dummy.map((item, index) => {
            return <li key={index}>
              <span className='icon icon-certificate'></span>
              <button onClick={() => onClick(index, true)}>
                <span>{item.title}</span>
                <i className='icon icon-table_f_ar_r'></i>
              </button>
            </li>
          })
        }
      </ul>

    </section>

    {// *   ,  
      toggle.state &&
      <div className='certificate-toggle__wrapper'
        onClick={() => onClick(0, false)}>

        <div className='toggle__container'>

          <div className='toggle-control'>
            <span>{dummy[toggle.index].title}</span>
            <button onClick={() => onClick(toggle.index, false)}>
              <i className="icon icon-close-white"></i>
            </button>
          </div>

          <div className='toggle-certificate'>
            <span className='img-certificate'>{dummy[toggle.index].img}</span>
          {/* <img src{dummy[toggle.index].img} alt className="certificate"/> */}
          </div>

        </div>
      </div>
    }

  </main>
}