import { Link } from "react-router-dom"

import { links } from "../../data/link";
// import { InputSearchBanner } from "./InputSearch";


export default function Banner({ page }) {
  const gnbDepth2 = [
    {
      h5: 'TmaxOS ',
      path: links.osp4111,
      img_banner: 'img_banner-subpage-1', // img class 
      span: [
        ' OS       .',
      ],
      ul: [
        {
          path: links.osp4111,
          text: '',
          p: [
            ' OS       .',
          ],
        },
        {
          path: links.osp4116,
          text: '',
          p: [
            '   SW    .',
          ],
        },
        {
          path: links.osp4117,
          text: ' ',
          p: [
            '   SW    .'
          ],
        },
      ]
    },
    {
      h5: '',
      path: links.osp5111,
      img_banner: 'img_banner-subpage-2',
      span: [
        'Tmax, SuperPCMaster    .',
      ],
      ul: [
        {
          path: links.osp5112,
          text: 'Tmax',
          p: [
            '   ',
            'TmaxOS       '
          ],
        },
        {
          path: links.osp5113,
          text: 'Tmax SD',
          p: [
            'Tmax          '
          ],
        },
        {
          path: links.osp5114,
          text: 'SuperRDS',
          p: ['Tmax SD      '],
        },
      ]
    },
    {
      h5: '',
      path: links.osp6111,
      img_banner: 'img_banner-subpage-3',
      span: [
        '      .',
      ],
      ul: [
        {
          path: links.osp6115,
          text: 'TmaxOS ',
          p: [', / ,        .']
        },
        {
          path: links.osp6112,
          text: '',
          p: ['  .']
        },
        {
          path: links.osp6114,
          text: '',
          p: ['   .']
        },
      ]
    },
    {
      h5: '',
      path: links.osp7111,
      img_banner: 'img_banner-subpage-4',
      span: [
        '    .',
      ],
      ul: [
        {
          path: links.osp7116,
          text: '',
          p: ['     .']
        },
        {
          path: links.osp7120,
          text: ' ',
          p: ['    .']
        },
        {
          path: links.osp7121,
          text: '',
          p: ['     .']
        },
        {
          path: links.osp7126,
          text: '',
          p: ['Tmax       .']
        }
      ]
    },
    {
      h5: 'Tmax ',
      path: links.osp0111,
      img_banner: 'img_banner-subpage-5',
      span: [
        'Tmax      .',
      ],
      ul: [
        {
          path: links.osp0112,
          text: ' ',
          p: [
            'Tmax       .'
          ],
        },
        {
          path: '/app/OSP0114',
          text: ' ',
          p: [
            '      .'
          ],
        },
        {
          path: '/app/OSP011401',
          text: ' ',
          p: [
            'Tmax     .'
          ],
        },
        {
          path: '/app/OSP011402',
          text: ' ',
          p: [
            'Tmax         .'
          ],
        }
      ]
    },
    {
      h5: ' ',
      path: '/app/OSP9112',
      img_banner: 'img_banner-subpage-6',
      span: [
        '   ',
      ],
      ul:[]
    }
  ];
  
  const inf = { 
    community: false,
    inputCate : {
      banner: false,
      placeholder: 'default',
    },
    download: false,
    ...page
  };

  const bannerClass = inf.community ? 'subpage-banner commu' : 'subpage-banner';

  const returnContent = (pageCode = -1) => {
    if (pageCode < 0) { //   .
      const { h5, span } = gnbDepth2[page.code - 4];
      return {
        head: h5, text: span, bannercode: -1
      }
    }
    else { //   . 
      const { text, p } = ul[pageCode];
      return {
        head: text, text: p, bannercode: pageCode
      }
    }
  };

  const makeulLi = (uls) => {
    return uls.map((li, index) => {
      return <li key={index}>
        <Link to={li.path}
          className={bannercode === index ? 'focused' : ''}
        >{li.text}</Link>
      </li>
    });
  };

  const { ul, img_banner } = gnbDepth2[page.code - 4]; // ex) OSP5111 - code: 5 -> 5-4  --> 
  const { head, text, bannercode } = returnContent(page.subcode);

  const uls = makeulLi(ul);

  return <>
    <section className={bannerClass}>
      <span className={"illust-subpage " + img_banner}></span>
      {/*   */}
      <div className='subpage-depth2'>
        <ul className='depth2__container w-default'>{uls}</ul>
      </div>
      {/*   */}
      <div className='subpage-cont w-default'>
        <h2>{head}</h2>
        <p className={inf.code === 5 && inf.subcode > -1 ? 'important': ' '}>
          {
            text.map((item, index) => {
              return <span key={index}>
                {item}
              </span>
            })
          }
        </p>
        {inf.download && 
          <a 
            href='#download' 
            className='btn-rounded-02 btn-download-tmaxgr' 
            download={false}
          >
             
          </a>
        }
        {/* {inf.inputCate.banner && <InputSearchBanner page={page} />} */}
      </div>
    </section>
  </>
}