import { useEffect } from "react";
import Banner from "../../components/common/Banner"
// import { tosProducts } from '../../data/memo';


export default function OSP5111() {
  
  const tosProducts = [
    {
      name: 'Tmax',
      logo_img: '',
      p: [
        '   ', 
        'TmaxOS    ', 
        ' '
      ],
      pMobile: [
        '   ', 
        ' TmaxOS   ', 
        '  '
      ],
      spec: {
        cpu: '2.5 GHz 2 core (Intel 3 ) ',
        disk: '20GB',
        memory: '8GB',
        res: '1024x768'
      }
    },
    {
      name: 'Tmax SD',
      logo_img: '',
      p: [
        'Tmax     ',
        '    '
      ],
      pMobile: [
        'Tmax   ',
        '    ',
        '  '
      ],
    },
    {
      name: 'SuperRDS',
      logo_img: '',
      p: [
        'Tmax SD ', 
        '    '
      ],
      pMobile: [
        'Tmax SD   ', 
        '   '
      ]
    }
  ];

  const page = {
    code: 5,
    subcode: -1
  };

  const scrollEvent = () => {
    const sectionProducts = document.querySelectorAll('.product');

    sectionProducts.forEach((product, index) => {
      // const productTop = product.getBoundingClientRect().top;
      const productBottom = product.getBoundingClientRect().bottom;
      const productHeight = product.offsetHeight;
      // const eventsStandard = Math.floor(productTop + productHeight);

      //  height - header height - (:  50%)
      const eventStandard = Math.floor(productBottom - 1164 - (productHeight / 2)); 

      // console.log(productHeight); // 720
      // console.warn(index);
      // console.log(eventStandard);
      // console.log(Math.floor(productBottom - 1164)); // 1080+84

      //  , --visible  
      if(eventStandard <= 0){
        product.classList.add('--visible');
      }
      //  , --visible  
      else{
        const removeStandard = product.classList.contains('--visible');

        if(removeStandard){
          product.classList.remove('--visible');
        }
      }
    });
  }

  useEffect(() => {
    window.addEventListener('scroll', scrollEvent);
    return () => {
      window.removeEventListener('scroll', scrollEvent);
    }
  }, [])

  return <>
    <Banner page={page} />

    <main className="main-osp5111">

      <section className="tmaxos-products background-01">
        <h3>
          <br />
          <strong>  .</strong>
        </h3>

        <div className="products-list__container w-default">
          <ul>
            {
              tosProducts.map((li, index) => {
                const { name, p, pMobile } = li;

                return <li key={index}>
                  <button className="btn-product">
                    <span className="product-name">{name}</span>
                    
                    <p className="product-cont --pcview">
                      {
                        p.map((pText, index) => {
                          return <span key={index}>{pText} <br /></span>
                        })
                      }
                    </p>

                    <p className="product-cont --mobile">
                      {
                        pMobile.map((pText, index) => {
                          return <span key={index}>{pText} <br /></span>
                        })
                      }
                    </p>

                    <div className="product-more">
                      <a className="btn btn-more" href="#moredetail">
                         
                        <i className="icon icon-more_arrow"></i>
                      </a>

                      {//   component   
                        index === 0 && 
                        <a className="btn btn-rounded-03" href="#download">
                          
                        </a>
                      }
                    </div>

                  </button>
                </li>
              })
            }
          </ul>
        </div>
      </section>

      <section className="product white">
        <div className="product__container w-default">
          <span className="img img_feature-1"></span>

          <div className="feature-content">
            <h4>
              . . .
              <strong>Tmax</strong>
            </h4>
            <p>
                   <br/>
                    <br/>
                   OS  .
            </p>
          </div>
        </div>
      </section>

      <section className="product gray">
        <div className="product__container w-default">

          <div className="feature-content">
            <h4>
               
              <strong>Tmax SD</strong>
            </h4>
            <p>
              Tmax      <br/>
                   .
            </p>
          </div>

          <span className="img img_feature-2"></span>


        </div>
      </section>

      <section className="product white">
        <div className="product__container w-default">
          <span className="img img_feature-3"></span>

          <div className="feature-content">
            <h4>
                <br /> 
                 
              <strong>SuperRDS</strong>
            </h4>
            <p>
              Tmax SD      .
            </p>
          </div>
        </div>
      </section>
    </main>
  </>
}