import { useState } from 'react';
import { Link } from 'react-router-dom';

import Banner from '../../components/common/Banner';
import { BoardNotice, QuestionBoard } from '../../components/CustomBoard';
import { TabBarStyle } from '../../components/common/TabStyle';

import { links } from '../../data/link';

export default function OSP7111() {
  const [tabNumber, setTabNumber] = useState(0);

  const btnList = [
    {
      name: '',
      span: '     .',
      icon: 'icon-custom-01',
      path: links.osp7116
    },
    {
      name: ' ',
      span: '    .',
      icon: 'icon-custom-02',
      path: links.osp7120
    },
    {
      name: '',
      span: '     .',
      icon: 'icon-custom-03',
      path: links.osp7121
    }
  ];

  const page = {
    code: 7,
    subcode: -1,
    community: false,
    inputCate: {
      banner: true,
      name: 'search',
      placeHolder: '  .',
    }
  };

  return <>
    <Banner page={page} />
    
    <main className='main-osp7111 custom'>
      <section className='products background-01'>
        <span className='bg_c-l'></span>
        <span className='bg_c-r'></span>
        <h2>  ?</h2>
        <ul className='menu-list'>
          {
            btnList.map((li, index) => {
              const { name, span, icon, path } = li;
              return <li key={index}>
                <Link to={path} className="btn-link-customer">
                  <i className={'icon ' + icon}></i>
                  <h6>{name}</h6>
                  <span>{span}</span>
                </Link>
              </li>
            })
          }
        </ul>
      </section>

      {/* 2 -     */}
      <section className='custom-products'>
        <h3>   .</h3>
        <ul className='products-list'>
          {
            ['Tmax', "TmaxSD", 'SuperRDS'].map((li, index) => {
              return <li key={index}>
                <button className='btn-product'>
                  <span className='icon-product'>{li}</span>
                  <i className='icon icon_costom-ar-r'></i>
                </button>
              </li>
            })
          }
        </ul>
      </section>

      <section className='custom-notice'>
        <h3></h3>
        <div className='board-wrap notice w-default'>
          <BoardNotice />
          <a className='btn-more' href='# '> <i className='icon icon-more_arrow'></i></a>
        </div>
      </section>

      <section className='custom-question'>
        <h3> </h3>
        <div className='board-wrap question w-default'>

          <TabBarStyle 
            props = {{
              list : [
                '', 'Tmax', 'SuperPCMaster', 'SuperVDS', ''
              ],
              tabNumber,
              setTabNumber
            }}
          />

          <QuestionBoard />
          <a className='btn-more' href='# '>  <i className='icon icon-more_arrow'></i></a>
        </div>
      </section>
    </main>
  </>
}