import React, { useState } from 'react'
import { Link } from 'react-router-dom';

import { 
  PromoteBoard, 
  PromoteNews, 
  Customer 
} from '../../components/Components';
import { IconMoreArrow } from '../../components/common/Icons';
import { links } from '../../data/link';

export default function OSP3111() {

  // temp slider arr
  const slider = [
    {
      h1: '  OS Tmax',
      p: "            OS      Tmax  OS    ."
    },
  ];

  const [ slideActive, setSlideActive ] = useState(0);

  const styleSlider = {
    transform: `translateX(-${slideActive * 100}%)`,
    transition: 'transform 0.6s'
  }

  const styleBar = { // responsive
    transform: `translateX(${slideActive * 100}%)`,
    transition: 'transform 0.3s'
  }

  return <>
    <section className="banner-slides">
      {
        slider.map((slide, index) => {
          return <div 
            key={index}
            className={`slider slide-0${index+1}`}
            style={styleSlider}
          >
            {
              slideActive === index &&
              <div 
                className="slider__content"
                // style={styleSliderContent}
              >
                <div className="content__container">
                  <h1>{slide.h1}</h1>
                  <p>{slide.p}</p>
                </div>

              </div>
            }
          </div>
        })
      }

      <div 
        className="slider__controller "
        style={{display: 'none'}}
      >
        <div className="controller__wrapper w-default">
          <div className="controller__container">
            <button 
              className={slideActive + 1 === 1 ? "slide-left --limit": 'slide-left'}
              onClick={() => {
                setSlideActive(slideActive - 1);
              }}
            >
              <i className="icon icon-banner_ar_l"></i>
            </button>
            
            <div className='container__pagination'>
              <span className="page-now">{slideActive + 1}</span>
              <i className='icon icon_banner-slash'>&#47; </i>
              <span className="page-total">{slider.length}</span>
            </div>

            <button
              className={slider.length === slideActive + 1 ? "slide-right --limit" : "slide-right"}
              onClick={() => {
                setSlideActive(slideActive + 1);
              }}
            >
              <i className="icon icon-banner_ar_r"></i>
            </button>
          </div>

          <div className="controller__bar">
            <span 
              className="bar"
              style={styleBar}
            ></span>
          </div>
        </div>
      </div>
    </section>

    <main className='main-osp3111'>
      <div className="main-notice">
        <div className="notice__container w-default">
          <span></span>

          <a className="btn-notice-now" href="#">, 2021 SW    </a>
          <a className="btn-notice-all" href={links.osp6112}>
            <span></span>
            <IconMoreArrow />
          </a>
        </div>
      </div>

      <section className="main-products">
        <div className="product__container w-default">
          <h3>
            <span> ,  </span>
            <span>   .</span>
          </h3>
          <div className="product__btns">
            <Link 
              to={links.osp5112} 
              className='btn-product'
              onClick={() => window.scrollBy(0)}
            >
              <span className="img img_feature-1"></span>
              <h4>Tmax</h4>
              <p>
                    
                 
              </p>
              <span className="btn-more"> <i className="icon icon-more_arrow"></i></span>
            </Link>
            <Link 
              to={links.osp5113} 
              className='btn-product'
              onClick={() => window.scrollBy(0)}
            >
              <span className="img img_feature-2"></span>
              <h4>Tmax SD</h4>
              <p>
                Tmax    
                     
              </p>
              <span className="btn-more"> <i className="icon icon-more_arrow"></i></span>
            </Link>
            <Link 
              to={links.osp5114} 
              className='btn-product'
              onClick={() => window.scrollBy(0)}
            >
              <span className="img img_feature-3"></span>
              <h4>SuperRDS</h4>
              <p>
                Tmax SD    
                  
              </p>
              <span className="btn-more"> <i className="icon icon-more_arrow"></i></span>
            </Link>
          </div>
        </div>
      </section>

      <PromoteBoard />
      <PromoteNews />
      <Customer />
    </main>
  </>
}
