import { useEffect } from 'react';
import Banner from '../../components/common/Banner';
import { TabBoxedStyle } from '../../components/common/TabStyle';


export default function OSP4116() {
  const page = {
    code: 4,// depth1 
    subcode: 1 // depth2
  };
  
  //     ,   
  const scrollEvent = () => {
    const sectionHistory = document.querySelectorAll('.history');
    const tabHeight = document.querySelector('.tab-boxed-fix').offsetHeight;

    sectionHistory.forEach((section, index) => {
      const sectionTop = section.getBoundingClientRect().top;
      const eventStandard = Math.floor(sectionTop - tabHeight - 10);

      if(eventStandard <= 0){
        section.classList.add('--active');

        if(index === 0){
          sectionHistory[1].classList.add('--active');
        }
      }
    })
  }


  useEffect(() => {
    window.addEventListener('scroll', scrollEvent);
    return () => {
      window.removeEventListener('scroll', scrollEvent);
    }
  }, []);

  
  return <>
    <Banner page={page} />

    <main className='main-osp4116'>

      <TabBoxedStyle props={{
        list: [
          ' ~ 2019',
          '2018 ~ 2015',
          '2010 ~ 2003'
        ],
        id: [
          'osp41162023',
          'osp41162018',
          'osp41162010'
        ],
        page: 'osp4116',
      }} />

      <h1>
         OS  
        <strong> OS     </strong>
      </h1>

      <section className='history__container'>

        <div className='history h--01 --right' id='osp41162023'>
          <span className='img img_history-1'></span>

          <div className='history-content'>
            <h3>OS  </h3>
            <ul>
              <li><p><strong>2023</strong>/  </p></li>
              <li><p><strong>2022</strong>   </p></li>
            </ul>
          </div>
          
        </div>

        <div className='history --left'>
          <span className='img img_history-2'></span>
          <div className='history-content'>
            <h3> OS </h3>
            <ul className='--pcview'>
              <li><p>Tmax <strong>2021</strong></p></li>
              <li><p>TmaxOS3 <strong>2019</strong></p></li>
            </ul>

            <ul className='--mobileview'>
              <li><p><strong>2021</strong>Tmax </p></li>
              <li><p><strong>2019</strong>TmaxOS 3 </p></li>
            </ul>
          </div>
        </div>
        <div className='history --right' id='osp41162018'>
          <span className='img img_history-3'></span>
          <div className='history-content'>
            <h3> SW  </h3>
            <ul>
              <li><p><strong>2018</strong>GS(Good Software) 1</p></li>
              <li><p><strong>2015</strong>TmaxOS  </p></li>
            </ul>
          </div>
        </div>
        <div className='history --left' id='osp41162010'>
          <span className='img img_history-4'></span>
          <div className='history-content'>
            <h3>OS  </h3>
            <ul className='--pcview'>
              <li><p> ,  SDS <strong>2010</strong></p></li>
              <li><p>  <strong>2003</strong></p></li>
            </ul>

            <ul className='--mobileview'>
              <li><p><strong>2010</strong> ,  SDS </p></li>
              <li><p><strong>2003</strong>  </p></li>
            </ul>
          </div>
        </div>
      </section>
    </main>
  </>
}