/**
 * 
 */

import { useState, useEffect } from 'react';


const TabBoxedStyle = ({props}) => {

  // default
  const tab = { 
    list: [
      ' ',
      '',
      ' '
    ],
    id: [
      'tab1',
      'tab2', 
      'tab3'
    ],
    page: 'osp4116',
    // activeTab: 0,
    ...props
  };

  const [ boxFocus, setBoxFocus] = useState(0); // box focus 
  const [ tabFix, setTabFix ] = useState(false); // tab   

  // // click box focus 
  // click scroll 
  const onClick = (event, index = 0, elementID) => {
    event.preventDefault(); 
    
    const element = document.querySelector(`#${elementID}`);
    const elementTop = Math.floor(window.scrollY + element.getBoundingClientRect().top);

    // fix  tab  
    // 98 tab height
    window.scrollTo(0, elementTop - 98);
  };

  const handleBoxFocus = (elementIDs) => {

    elementIDs.forEach((elementID, index) => {
      const element = document.querySelector(`#${elementID}`);
      const elementTop = element.getBoundingClientRect().top; 
      const elementHeight = element.offsetHeight; // elemtn 

      // scroll   : 99 (98    )
      const eventStandard = Math.floor(elementTop + elementHeight - 99); 

      //   , activeIndex update
      if(eventStandard >= 0 && eventStandard <= elementHeight){
        // setActiveIndex = Number(index);
        setBoxFocus(Number(index));

      }

    });
  };


  // scroll tab fix 
  const onScroll = (event) => {
    const header = document.querySelector('.header');

    // *    
    // osp : 
    //  margin  css 
    const osp = document.querySelector(`.main-${tab.page}`);

    const bannerHeight = document.querySelector('.subpage-banner').offsetHeight - 25;
    // console.log(bannerHeight); // 464(web) // 231(mobile)

    // console.log(window.scrollY); // 450
    if(window.scrollY < (bannerHeight)){ 
      header.style.transform = `translateY(0%)`;
    }

    else if(window.scrollY > (bannerHeight) && window.scrollY <= (bannerHeight+100)){
      header.style.transform = `translateY(${(bannerHeight)-window.scrollY}%)`;
      setTabFix(false);
      osp.classList.remove('fixed');
    }

    // * tab fix 
    else if(window.scrollY > (bannerHeight+100)){
      header.style.transform = 'translateY(-100%)';

      osp.classList.add('fixed');
      setTabFix(true);

      // fix ,  box focus 
      // setBoxFocus(handleBoxFocus(tab.id, boxFocus));
      handleBoxFocus(tab.id, boxFocus);
    }

    else{
      osp.classList.remove('fixed');
      setTabFix(false);
    }
  };

  // component mount  event listener   
  useEffect(() => {
    window.addEventListener('scroll', onScroll);
    return () => {
      window.removeEventListener('scroll', onScroll);
    }
  }, [])


  return <div className={tabFix ?'tab-boxed-fix fixed':'tab-boxed-fix'}>
  <ul 
    className="tab-list"
    // onClick={(event) => onClick(event)}
  >
    {
      tab.list.map((item, index) => {
        return <li key={index} 
          className={index === boxFocus ? 'focused': ' '}
        >
          <a 
            href={'#'+tab.id[index]}
            // value={index}
            onClick={(event) => onClick(event, index, tab.id[index])}
          >
          {item}</a>
        </li>
      })
    }
  </ul>
</div>
}


/**
 * *   Tab
 * @exports to OSP4111 (TmaxOS )
 * 
 * todo: setTabNumber  default   ...
 */

const TabBarStyle = ({props}) => {

  const tab = {
    list: [
      'tab01',
      'tab02',
      'tab03'
    ],
    commu: false,
    alert: false,
    ...props
  };

  const onClick = (event) => {
    const { target: { value } } = event;
    
    if (value) {
      props.setTabNumber(Number(value));
    }
  }

  return <>
  <div className={tab.commu ? 'tab__bar-style commu' : 'tab__bar-style'}>
    <ul 
      className={`w-default tab-size-${tab.list.length}`}
      onClick={(event) => onClick(event)}
    >
      {
        tab.list.map((item, index) => {
          return <li key={index}>
            <button
              value={index}
              className={props.tabNumber === index ? 'focused' : ''}
            >{item}

            {/*    */}
            <span className={props.alert && index === 2 ? "alert-new": "alert-new --n"}>2</span>
            </button></li>
        })
      }
    </ul>
  </div>
  </> 
}

export { TabBoxedStyle, TabBarStyle };