const ContNews = () => {
  return <>
    <a href="#1" className='item-card'>
      <div className='wrap-img'>
        <img src={process.env.PUBLIC_URL + "/asset/images/01.png"} alt="temp_image" />
      </div>
      <strong>[ ] , 2021  SW    </strong>
      <span className='date'>2021.12.13</span>
    </a>
  </>
}

//  
const ContVideo = () => {
  return<>
    <a className='item-card --video' href="#1">
      
    <div className='wrap-img'>
      <img src={process.env.PUBLIC_URL + "/asset/images/01.png"} alt="temp_image" />
    </div>
    <div className='content'>
      <i className='icon icon-play'></i>
      <strong>   , As Super as possible</strong>
    </div>
  </a>
  </>
}

export {ContNews, ContVideo};