import { ButtonMore } from './button/Buttons';
import { links } from '../data/link';


//  OSP3111 |  OSP6111
const PromoteBoard = () => {
  const props = {
    path: links.osp6115,
    content: '   '
  };

  return <>
    <section className="main-board">
      <div className="board__container w-default">
        <h3>
          <span></span>
          <span><strong> </strong> .</span>
        </h3>

        <div className="board-cards">
          <button>
            <img src={process.env.PUBLIC_URL + "/asset/images/01.png"} alt="board-0" />
            <h5 className="board-title">, 2021  SW    </h5>
          </button>

          <button>
            <img src="" alt="board-1" />
            <h5 className="board-title">, 2021  SW    </h5>
          </button>

          <button>
            <img src="" alt="board-2" />
            <h5 className="board-title">, 2021  SW    </h5>
          </button>

          <button>
            <img src="" alt="board-3" />
            <h5 className="board-title">, 2021  SW    </h5>
          </button>
        </div>
        <ButtonMore props={props} />
      </div>
    </section>
  </>
}

